@charset "utf-8";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  width: min(100% - 30px, 1080px);
  margin-inline: auto;
}

.flex {
  display: flex;
}

.spacebetween {
  justify-content: space-between;
}

.center {
  display: grid;
  place-items: center;
}

body {
  font-family: 'Nunito', sans-serif, Arial, sans-serif;
  background-color: rgb(24, 23, 32);
}

.hidden {
  display: none;
}

ul {
  gap: 20px;
}

/*
  Header
*/

header {
  height: 60px;
  background-color: rgb(56, 75, 183);
}

h1 {
  font-size: 26px;
  font-weight: 500;
  color: #fff;
  line-height: 60px;
}

nav ul li {
  line-height: 60px;
  list-style: none;
}

li {
  font-size: 18px;
  font-weight: 600;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

nav ul li:hover, .links i:hover {
  color: rgb(199, 199, 237);
}

/*
  Content
*/

button {
  width: 140px;
  height: 36px;
  font-size: 16px;
  font-weight: 500;
  border-radius: 6px;
  border: none;
  cursor: pointer;
}

.primary {
  background-color: aqua;
}

.default {
  background-color: rgb(255 255 255 / 0.05);
}

img {
  height: 240px;
}

/*
  Footer
*/

footer {
  height: 100px;
  background-color: #16192d;
}

.end {
  line-height: 100px;
}

.links ul li {
  list-style: none;
}

.links i {
  color: #fff;
  font-size: 20px;
  transition: all 0.25s ease-in-out;
}

.copyright {
  font-size: 17px;
  color: #fff;
}